import React from "react";
import ProjectCard from "./ProjectCard";

function ProjectSection(props) {
  const img1 = require("../images/project2.jpg");
  const img2 = require("../images/project3.jpg");
  const projects = [
    {
      id: 1,
      title: "Project 1",
      imageurl: img1,
      content: "This is my project about...",
    },
    {
      id: 2,
      title: "Project 2",
      imageurl: img2,
      content: "This is my project about...",
    },
    {
      id: 3,
      title: "Project 3",
      imageurl:
        "data:image/jpeg;base64,",
      content: "This is my project about...",
    },
  ];
  return (
    // <!-- Projects section  -->
    <div className="container text-center my-5">
      <h1 className="font-weight-light">
        My <span className="text-info">Projects</span>
      </h1>
      <div className="lead">I build products. Just like this website</div>
      <div className="row my-5 pt-3">
        {projects.map((project) => (
          <div key={project.id} className="col-12 col-md-4 my-2">
            <ProjectCard project={project} />
          </div>
        ))}
      </div>

      <div className="my-5">
        <a href="/" className="text-dark text-right">
          <h5>
            See my projects
            <i className="fas fa-arrow-right align-middle"></i>
          </h5>
        </a>
      </div>
    </div>
  );
}
export default ProjectSection;
